multiGroupBy = (array, group, ...restGroups) => {
  if(!group) {
    return array;
  }
  const currGrouping = _.groupBy(array, group);
  if(!restGroups.length) {
    return currGrouping;
  }
  return _.transform(currGrouping, (result, value, key) => {
    result[key] = this.multiGroupBy(value, ...restGroups);
  }, {});
};

make_pattern_list = () =>{
  try {
   const pattern_list = {};
   const rows = this.state.rawdata;
   let temp_dest_1;
   let temp_dest_2;
   let temp_sku_id;

   rows.forEach((row, idx) => {
     if (idx > 0) {
       const destination_1 = row[1];
       const sku_id = row[11];
       const destination_2 = row[12];
       const pcs = row[13];
       const style  = row[7];
       const size   = row[9];
       const color  = row[10];
       const sku_list = {
           'style': style,
           'size': size,
           'color': color,
           'pcs':pcs
       };
       temp_dest_1 = destination_1;
       temp_dest_2 = destination_2;
       
       if (pattern_list[destination_1] === undefined) {
         pattern_list[destination_1] = {};
       }
       if (pattern_list[destination_1][destination_2] === undefined) {
         pattern_list[destination_1][destination_2] = {};
       }
       pattern_list[destination_1][destination_2][sku_id] = sku_list;
     }
   });

   console.log("pattern_list", pattern_list);
   this.state.pattern_list = pattern_list;
   this.setState({pattern_list:pattern_list});
   const sku_count = Object.keys(pattern_list[temp_dest_1][temp_dest_2]).length;
   console.log("sku_count:",  sku_count);

   // delete duplicate
   const unique_pattern_list = new Array;
   let pre_dest_number;
   for (temp_dest_1 in pattern_list) {
     for (temp_dest_2 in pattern_list[temp_dest_1]) {
       if (pre_dest_number === undefined) {
         pre_dest_number = temp_dest_2;
         // insert first pattern
         unique_pattern_list.push(pattern_list[temp_dest_1][temp_dest_2]);
       } else {
         // check all pattern
         let add_flg = true;
         const now_data = pattern_list[temp_dest_1][temp_dest_2];

         for (let i = 0; i < unique_pattern_list.length; ++i) {
           const pre_pattern = unique_pattern_list[i];
           let all_same_flg = true;

           for (temp_sku_id in pattern_list[temp_dest_1][temp_dest_2]) {
             if (pre_pattern[temp_sku_id].pcs !== now_data[temp_sku_id].pcs) {
               all_same_flg = false;
               break;
             }
           }
           if (all_same_flg === true) {
             add_flg = false;
             break;
           }
         }

         if (add_flg) {
           unique_pattern_list.push(now_data);
         }
       }
     }
   }
   this.setState({unique_pattern_list});
   // console.log("unique_pattern_list", unique_pattern_list);
   let patternFileNo = 1;
   _.each(unique_pattern_list,(pattrn,pattrnid)=>{
     const savePath = path.join(__dirname , '../',`Template_File/shimamura_ptn_${ patternFileNo.toString() }.xlsx`);
     this.currentWorksheet = this.workbook.getWorksheet('PT');
     this.func_put_pt(pattrn);
     
     this.workbook.xlsx.writeFile(savePath).then((result)=>{
       console.log('response', result)
     });
     patternFileNo +=1; 
   });
   

 } catch (error) {
   console.log(error);
 }
}

