func_put_pt = (sku_list) => {
        this.currentWorksheet = this.workbook.getWorksheet('PT');
       
        let rowOffset = 4;
        let pcsCount =0;
        _.each(sku_list,(sku, skuId) => { 
                      pcsCount +=sku.pcs;
                      this.currentWorksheet.getCell(`A${rowOffset.toString()}`).value = sku.style;
                      this.currentWorksheet.getCell(`B${rowOffset.toString()}`).value = skuId;
                      this.currentWorksheet.getCell(`C${rowOffset.toString()}`).value = sku.size;
                      this.currentWorksheet.getCell(`D${rowOffset.toString()}`).value = sku.color;
                      this.currentWorksheet.getCell(`F${rowOffset.toString()}`).value = sku.pcs;
                      rowOffset +=1;   
                  });
                  this.currentWorksheet.getCell("F1").value = pcsCount;
                  this.func_put_area_checklist();
              };

func_put_area_checklist = () => {
   let pattern_list = this.state.pattern_list;
   console.log(pattern_list);
  _.each(pattern_list,(areaPattern, areaIndex)=>{
    console.log(areaIndex);
    this.currentWorksheet = this.workbook.getWorksheet(areaIndex.toString());
    let rowOffset = 4;
    let colOffset = 0;
    let pcsCount =0;
    let cellStyle = this.currentWorksheet.getCell("A4").style;
    const serialCols = ["A","B","C","D","E","F","G","H","I","J"];
    _.each(areaPattern,(pattern, patternId) => { 
                  let cellnumber = `${serialCols[colOffset]}${rowOffset.toString()}`;
                  console.log("cellnumber",cellnumber);
                  this.currentWorksheet.getCell(cellnumber).value = _.padStart(patternId.toString(),4,'0');
                  this.currentWorksheet.getCell(cellnumber).style = cellStyle;
                  if((serialCols.length-1)==colOffset)
                  {
                    rowOffset +=1;
                    colOffset =0;  
                  }
                  else
                  {
                    colOffset +=1;  
                  }
                  
              });
              this.currentWorksheet.getCell("F1").value = pcsCount;
  
  })
 };

